import React, {FC, useEffect, useState} from 'react';
import {useAppDispatch, useAppSelector} from "../hooks/redux";
import {getGames} from "../store/reducers/games/gameActions";
import axios from "axios";
import {IGame} from "../models/IGame";
import {ModalForm} from './ModalForm';
import {IProfileState} from "../store/reducers/profile/profileSlice";
import {ApiService} from "../api";
import spinner from "../assets/spinner.svg";
import {COUNTRIES} from '../assets/consts'


const LeagueItem: FC<{
    result: any, league: any, showParam: string, handleChangeShowModal: (val: boolean) => void, handleSetCurrentGame: (val: IGame) => void, handleSetCurrentBet: ({
                                                                                                                                                                      kf,
                                                                                                                                                                      name,
                                                                                                                                                                      id
                                                                                                                                                                  }: { kf: number, name: string, id: number }) => void
}> = ({
          result,
          league,
          showParam,
          handleChangeShowModal,
          handleSetCurrentGame,
          handleSetCurrentBet
      }) => {
    return (
        <>
            <div className="toc-title">
                <div className="global-ico gi-star"/>
                <div className="global-ico gi-rus"/>
                <span>{league[1]}</span>
            </div>
            {
                result && Object.keys(result.country)
                    .map(co => {
                        // @ts-ignore
                        return Object.keys(result.country[co])
                            .map(sport => {
                                // @ts-ignore
                                return Object.keys(result.country[co][sport])
                                    .map(status => {
                                        // @ts-ignore
                                        return Object.keys(result.country[co][sport][status])
                                            .map((game, index) => {
                                                return (
                                                        result.country[co][sport][status][game].league.id === String(league[0])
                                                    ) &&
                                                    <GameItem
                                                        ind={index}
                                                        game={result.country[co][sport][status][game]}
                                                        showParam={showParam}
                                                        handleChangeShowModal={handleChangeShowModal}
                                                        handleSetCurrentGame={handleSetCurrentGame}
                                                        handleSetCurrentBet={handleSetCurrentBet}
                                                    />
                                            })
                                    })
                            })
                    })
            }
        </>
    )
}

const GameItem: FC<{
    ind: number, game: IGame, showParam: string, handleChangeShowModal: (val: boolean) => void, handleSetCurrentGame: (val: IGame) => void, handleSetCurrentBet: ({
                                                                                                                                                                      kf,
                                                                                                                                                                      name,
                                                                                                                                                                      id
                                                                                                                                                                  }: { kf: number, name: string, id: number }) => void
}> = ({
          ind,
          game,
          showParam,
          handleChangeShowModal,
          handleSetCurrentGame,
          handleSetCurrentBet
      }) => {

    const currentDate = new Date(new Date().getTime() + 24 * 60 * 60 * 1000)

    return (
        <div className="toc-item">
            <div className="toc-i-left-side">
                <div className="global-ico gi-star"/>
                <div className="toc-i-time">
                    <div className="tocit-daypart">
                        {
                            (Number(
                                game
                                    .beautiful_time_start
                                    .split(' ')[0]
                                    .split('-')[2]
                            ) - Number(currentDate.getDate())) === 0 ? ' ' :
                            (Number(
                                game
                                    .beautiful_time_start
                                    .split(' ')[0]
                                    .split('-')[2]
                            ) - Number(currentDate.getDate())) === 1 ? ' ' :
                                game
                                    .beautiful_time_start
                                    .split(' ')[0]
                        }
                    </div>
                    <div className="tocit-time">{game.beautiful_time_start.split(' ')[1]}</div>
                </div>
                <div className="toc-i-teams">
                    <div>
                        {/*<div className="global-ico gi-zenit"/>*/}
                        <img src={game.home_team_logo} height={14} alt=".."/>
                        {game.name.split(' VS ')[0]}
                    </div>
                    <div>
                        {/*<div className="global-ico gi-zenit"/>*/}
                        <img src={game.away_team_logo} height={14} alt=".."/>
                        {game.name.split(' VS ')[1]}
                    </div>
                </div>
            </div>
            <div className="toc-i-right-side">
                <div className="tocirs-someinfo"><span> </span></div>
                {
                    showParam !== '' ?
                        <>
                            <div className="tocirs-flcol tocirs-matchtime">
                                <div className="tocirsmt-title">placeholder</div>
                                <div className="tocirsmt-line"> </div>
                            </div>
                            <div className="tocirs-flcol tocirs-koef">
                                <div className="tocirsmt-title">
                                    {
                                        game.name.split(' VS ')[0]
                                    }
                                </div>
                                <div
                                    onClick={() => {
                                        handleSetCurrentGame(game)
                                        handleChangeShowModal(true)
                                        handleSetCurrentBet({
                                            name: '1',
                                            //@ts-ignore
                                            kf: game.quotes && game.quotes[' ( )'][0]["kf"],
                                            //@ts-ignore
                                            id: game.quotes && game.quotes[' ( )'][0]["id"]
                                        })
                                    }}
                                    className="tocirsmt-line"
                                >
                                    {
                                        game.quotes && game.quotes[' ( )'][0]["kf"]
                                    }
                                </div>
                            </div>
                            <div className="tocirs-flcol tocirs-koef">
                                <div className="tocirsmt-title">
                                    {
                                        game.quotes && game.quotes[' ( )'][1]["name"]
                                    }
                                </div>
                                <div
                                    onClick={() => {
                                        handleSetCurrentGame(game)
                                        handleChangeShowModal(true)
                                        handleSetCurrentBet({
                                            name: '',
                                            //@ts-ignore
                                            kf: game.quotes && game.quotes[' ( )'][1]["kf"],
                                            //@ts-ignore
                                            id: game.quotes && game.quotes[' ( )'][1]["id"]
                                        })
                                    }}
                                    className="tocirsmt-line"
                                >
                                    {
                                        game.quotes && game.quotes[' ( )'][1]["kf"]
                                    }
                                </div>
                            </div>
                            <div className="tocirs-flcol tocirs-koef">
                                <div className="tocirsmt-title">
                                    {
                                        game.name.split(' VS ')[1]
                                    }
                                </div>
                                <div
                                    onClick={() => {
                                        handleSetCurrentGame(game)
                                        handleChangeShowModal(true)
                                        handleSetCurrentBet({
                                            name: '2',
                                            //@ts-ignore
                                            kf: game.quotes && game.quotes[' ( )'][2]["kf"],
                                            //@ts-ignore
                                            id: game.quotes && game.quotes[' ( )'][2]["id"]
                                        })
                                    }}
                                    className="tocirsmt-line"
                                >
                                    {
                                        game.quotes && game.quotes[' ( )'][2]["kf"]
                                    }
                                </div>
                            </div>
                        </> :
                        <>
                            <div className="tocirs-flcol tocirs-matchtime">
                                <div className="tocirsmt-title">placeholder</div>
                            </div>
                            <div className="totals">
                                <div>
                                    <h3></h3>
                                </div>
                                <div style={{display: 'flex'}}>
                                    <div style={{display: 'flex', flexDirection: 'column'}}>
                                        {
                                            // @ts-ignore
                                            game.quotes && game.quotes['']
                                                .map((item: any, index: number) => {
                                                    if (index % 2 === 0) return <div className='total'>
                                                        {item.name}
                                                    </div>
                                                })
                                        }

                                    </div>
                                    <div style={{display: 'flex', flexDirection: 'column'}}>
                                        {
                                            // @ts-ignore
                                            game.quotes && game.quotes['']
                                                .map((item: any, index: number) => {
                                                    if (index % 2 === 1) return <div className='total'>
                                                        {item.name}
                                                    </div>
                                                })
                                        }
                                    </div>
                                </div>
                            </div>
                        </>
                }
            </div>
        </div>
    )
}


const FilterCountry: FC<any> = ({handleChangeParams, params}) => {
    const [countries, setCountries] = useState({})

    useEffect(() => {
        const fetchCountries = async () => {
            const {data} = await axios.get('http://gpbetapi.ru/country')
            setCountries(data)
        }
        fetchCountries()
    }, [])

    if (params.sport_name === 'all') return (
        <div id="filter-contry">
            <div
                onClick={() => handleChangeParams({...params, country: 'all'})}
                className="one-contry-f"
            >
                
            </div>
            {
                Object.keys(COUNTRIES)
                    .map(co => {
                        // @ts-ignore
                        return (
                            <div
                                onClick={() => handleChangeParams({...params, country: co})}
                                className="one-contry-f"
                                style={{
                                    display: 'flex',
                                    justifyContent: 'space-evenly',
                                    alignItems: 'center',
                                    flexDirection: 'column',
                                    maxHeight: 60
                                }}
                            >
                                <img src={
                                    // @ts-ignore
                                    COUNTRIES[co]['svg_url']
                                }
                                     height={20}
                                     alt='cc'
                                />
                                {
                                    // @ts-ignore
                                    COUNTRIES[co].ru_name
                                }
                            </div>
                        )
                    })
            }
        </div>
    )

    return (
        <div id="filter-contry">
            <div
                onClick={() => handleChangeParams({...params, country: 'all'})}
                className="one-contry-f"
            >
                
            </div>
            {
                Object.keys(countries)
                    .map(sport => {
                        if (sport === params.sport_name){

                            // @ts-ignore
                            return Object.keys(countries[sport])
                                .map(co => {
                                    return (
                                        <div
                                            onClick={() => handleChangeParams({...params, country: co})}
                                            className="one-contry-f"
                                            style={{
                                                display: 'flex',
                                                justifyContent: 'space-evenly',
                                                alignItems: 'center',
                                                flexDirection: 'column',
                                                maxHeight: 60
                                            }}
                                        >
                                            <img src={
                                                // @ts-ignore
                                                COUNTRIES[co]['svg_url']
                                            }
                                                 height={20}
                                                 alt='cc'
                                            />
                                            {
                                                // @ts-ignore
                                                COUNTRIES[co].ru_name
                                            }
                                        </div>
                                    )
                                })
                        }
                    })
            }
        </div>
    )

    // return (
    //     <div id="filter-contry">
    //         {/*{*/}
    //         {/*    //@ts-ignore*/}
    //         {/*    countries*/}
    //         {/*    // @ts-ignore*/}
    //         {/*    // countries && countries[params.sport_name]*/}
    //         {/*    //     .map((co: any) => {*/}
    //         {/*    //         return (*/}
    //         {/*    //             <div*/}
    //         {/*    //                 onClick={() => handleChangeParams({...params, country: co})}*/}
    //         {/*    //                 className="one-contry-f"*/}
    //         {/*    //             >*/}
    //         {/*    //                 /!*<img*!/*/}
    //         {/*    //                 /!*    src={*!/*/}
    //         {/*    //                 /!*        //@ts-ignore*!/*/}
    //         {/*    //                 /!*        COUNTRIES[co].svg_url*!/*/}
    //         {/*    //                 /!*    }*!/*/}
    //         {/*    //                 /!*    alt={'im'}*!/*/}
    //         {/*    //                 /!*/>*!/*/}
    //         {/*    //                 {*/}
    //         {/*    //                     //@ts-ignore*/}
    //         {/*    //                     COUNTRIES[co].ru_name*/}
    //         {/*    //                 }*/}
    //         {/*    //             </div>*/}
    //         {/*    //         )*/}
    //         {/*    //     })*/}
    //         {/*}*/}
    //     </div>
    // )
}

const FilterCase: FC<any> = ({sport, handleChangeShowParam}) => {
    const sports = {
        'soccer': '',
        'icehockey': ''
    }
    return (
        <div className="filter-name-cases">
            <div className="fl-name">
                <div className="global-ico gi-star"/>
                <div className="global-ico gi-football"/>
                <span>
                    {                // @ts-ignore
                        sports[sport]}
                </span></div>
            <div className="fl-cases">
                <div
                    onClick={() => handleChangeShowParam(' ( )')}
                    className="one-fl-case"><span></span></div>
                <div
                    onClick={() => handleChangeShowParam('')}
                    className="one-fl-case"><span></span></div>
                {/*<div className="one-fl-case"><span> </span></div>*/}
                {/*<div className="one-fl-case"><span></span></div>*/}
                {/*<div className="one-fl-case"><span> </span></div>*/}
            </div>
        </div>
    )
}


const Main: FC = () => {
    const [profile, setUserInfo] = useState<IProfileState>({
        error: false,
        message: null,
        result: null,
    })
    const {result} = useAppSelector(state => state.gameReducer)
    const {session} = useAppSelector(state => state.authReducer)
    const [leagueList, setLeagueList] = useState({})
    const dispatch = useAppDispatch()
    const [isLoading, setIsLoading] = useState(false)
    const [params, setParams] = useState({
        sport_name: 'all',
        quotes: 'all',
        country: 'all',
        league_id: 'all',
        days: 10,
        one_day: 0,
        sort_number: false,
        beautiful_time_start: '- -'
    })
    const [currentGame, setCurrentGame] = useState<IGame | null>(null)
    const [currentBet, setCurrentBet] = useState({
        name: '',
        kf: 0,
        id: 0
    })
    const [showParam, setShowParam] = useState(' ( )')
    const [showModal, setShowModal] = useState(false)

    const handleChangeParams = (params: {
        sport_name: string,
        quotes: string,
        country: string,
        league_id: string,
        days: number,
        one_day: number,
        sort_number: boolean
        beautiful_time_start: string,
    }) => {
        setParams({...params})
    }

    const handleChangeShowParam = (param: string) => {
        setShowParam(param)
    }

    const handleChangeShowModal = (value: boolean) => {
        setShowModal(value)
    }

    const handleSetCurrentGame = (bid: IGame) => {
        setCurrentGame({...bid})
    }

    const handleSetCurrentBet = ({name, kf, id}: { name: string, kf: number, id: number }) => {
        setCurrentBet({name, kf, id})
    }

    useEffect(() => {
        setIsLoading(true)
        dispatch(getGames({...params, game_status: 'not started'}))
        const fetchLeagueList = async () => {
            const {data} = await axios.post('http://gpbetapi.ru/league_list', {
                league_sport: params.sport_name,
                league_cc: 'all'
            })
            setLeagueList(data)
            setIsLoading(false)
        }
        fetchLeagueList()
    }, [params])

    useEffect(() => {
        const fetchUserInfo = async (session: string) => {
            const data = await ApiService.getProfile(session)
            setUserInfo(data)
        }
        if (session) {
            fetchUserInfo(session)
        }
    }, [])

    // @ts-ignore
    return (
        <div id="content-wr">
            <div id="two-left">

                <div id="page-title">
                    <div id="pt-text"> </div>
                    <div id="pt-stripe"/>
                </div>

                <div id="rec-menu">
                    <div
                        onClick={() => handleChangeParams({...params, sport_name: 'soccer'})}
                        className="one-rec-menu">
                        <div className="global-ico gi-football"/>
                        <div className="orm-title"></div>
                    </div>
                    <div
                        onClick={() => handleChangeParams({...params, sport_name: 'icehockey'})}
                        className="one-rec-menu">
                        <div className="global-ico gi-hockey"/>
                        <div className="orm-title"></div>
                    </div>
                </div>


                <div id="table-main">
                    {
                        currentGame && <ModalForm
                            handleChangeShowModal={handleChangeShowModal}
                            showModal={showModal}
                            currentGame={currentGame}
                            bet={currentBet}
                            user={profile}
                            //@ts-ignore
                            session={session}
                        />
                    }

                    <FilterCountry
                        handleChangeParams={handleChangeParams}
                        params={params}
                    />
                    <FilterCase
                        sport={params.sport_name}
                        handleChangeShowParam={handleChangeShowParam}
                    />


                    {
                        isLoading ?
                            <div style={{display: 'flex', alignItems: 'center', justifyContent: 'center'}}>
                                <img src={spinner} alt="Loading results..."/>
                            </div>
                            :
                            <div className="table-one-cat">
                                {
                                    Object.keys(leagueList)
                                        .map(sp => {
                                            // @ts-ignore
                                            return Object.keys(leagueList[sp])
                                                .map(co => {
                                                    // @ts-ignore
                                                    return leagueList[sp][co]
                                                        // eslint-disable-next-line array-callback-return
                                                        .map((league: any[]) => {
                                                            if (params.country === 'all') {
                                                                return <LeagueItem
                                                                    league={league}
                                                                    result={result}
                                                                    showParam={showParam}
                                                                    handleChangeShowModal={handleChangeShowModal}
                                                                    handleSetCurrentGame={handleSetCurrentGame}
                                                                    handleSetCurrentBet={handleSetCurrentBet}
                                                                />
                                                            } else if (params.country === co) {
                                                                return <LeagueItem
                                                                    league={league}
                                                                    result={result}
                                                                    showParam={showParam}
                                                                    handleChangeShowModal={handleChangeShowModal}
                                                                    handleSetCurrentGame={handleSetCurrentGame}
                                                                    handleSetCurrentBet={handleSetCurrentBet}
                                                                />
                                                            }
                                                        })
                                                })
                                        })
                                }

                            </div>
                    }
                </div>

            </div>

            <div id="two-right">
                <h2>[-]</h2>
                <div id="pop-sob">
                    <div id="pop-sob-title"> </div>
                    <div id="pop-sob-menu">
                        <div className="one-ps-menu active">
                            <div className="global-ico gi-football"></div>
                            <div className="psm-title"></div>
                        </div>
                        <div className="one-ps-menu">
                            <div className="global-ico gi-hockey"></div>
                            <div className="psm-title"></div>
                        </div>
                        <div className="one-ps-menu">
                            <div className="global-ico gi-basketball"></div>
                            <div className="psm-title"></div>
                        </div>
                        <div className="one-ps-menu">
                            <div className="global-ico gi-football"></div>
                            <div className="psm-title"></div>
                        </div>
                        <div className="one-ps-menu">
                            <div className="global-ico gi-hockey"></div>
                            <div className="psm-title"></div>
                        </div>
                        <div className="one-ps-menu">
                            <div className="global-ico gi-basketball"></div>
                            <div className="psm-title"></div>
                        </div>
                    </div>
                    <div className="one-pop-sob">
                        <div className="pop-sob-title-in">.  </div>
                        <div className="pop-sob-teams">
                            <div>
                                <div className="global-ico gi-zenit"></div>
                                 
                            </div>
                            <div className="pst-hl"></div>
                            <div>
                                <div className="global-ico gi-zenit"></div>
                                 -
                            </div>
                        </div>
                        <div className="pop-sob-koef">
                            <div className="psk-one">
                                <div className="psko-title"> 1</div>
                                <div className="psko-number">15.00</div>
                            </div>
                            <div className="psk-one">
                                <div className="psko-title"></div>
                                <div className="psko-number">15.00</div>
                            </div>
                            <div className="psk-one">
                                <div className="psko-title"> 2</div>
                                <div className="psko-number">15.00</div>
                            </div>
                        </div>
                    </div>
                    <div className="all-pop-sob"> 135 
                        <div className="global-ico gi-arrow-right"></div>
                    </div>
                </div>

                <h2>[-]</h2>
                <div id="right-col-menu">
                    <div className="one-rcm-menu">
                        <div className="global-ico gi-football"></div>
                        <div className="rcm-title"></div>
                    </div>
                    <div className="one-rcm-menu">
                        <div className="global-ico gi-hockey"></div>
                        <div className="rcm-title"></div>
                    </div>
                    <div className="one-rcm-menu">
                        <div className="global-ico gi-basketball"></div>
                        <div className="rcm-title"></div>
                    </div>
                    <div className="one-rcm-menu">
                        <div className="global-ico gi-volleyball"></div>
                        <div className="rcm-title"></div>
                    </div>
                    <div className="one-rcm-menu">
                        <div className="global-ico gi-tennis"></div>
                        <div className="rcm-title"></div>
                    </div>
                    <div className="one-rcm-menu">
                        <div className="global-ico gi-baseball"></div>
                        <div className="rcm-title"></div>
                    </div>
                    <div className="one-rcm-menu">
                        <div className="global-ico gi-american-f"></div>
                        <div className="rcm-title"> </div>
                    </div>
                    <div className="one-rcm-menu">
                        <div className="global-ico gi-golf"></div>
                        <div className="rcm-title"></div>
                    </div>
                    <div className="one-rcm-menu">
                        <div className="global-ico gi-rus-biliard"></div>
                        <div className="rcm-title"> </div>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default Main;
